.warp {
  background-color: #ffffff;
  height: 100vh;
  font-family: PingFangSC-Medium, PingFang SC;
  overflow: hidden;
}
.header {
  background-color: #010101;
  color: #ffffff;
  height: 60px;
  width: 100%;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  line-height: 60px;
}
.body_header {
  width: 100%;
  border-bottom: 1px solid #efefef;
  padding: 14px 12px;
}
.body_header .body_header_title {
  font-size: 16px;
  color: #000000;
}
.body_header .body_header_doc {
  padding-top: 4px;
  color: #909090;
}
.body_content {
  font-weight: 400;
}
.body_content .body_content_item {
  padding: 14px 12px;
  padding-right: 0;
  display: flex;
  margin-left: 12px;
  border-bottom: 1px solid #efefef;
  justify-content: space-between;
}
.body_content .body_content_item .left {
  color: #8e8e8e;
}
.body_content .body_content_item .right {
  padding-right: 12px;
}
